.hero {
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.hero-slider {
  position: relative;
  width: 100%;
  height: 100%;
}

.slides-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  will-change: transform, opacity;
  opacity: 0;
  clip-path: inset(0 100% 0 0);
}

.slide.active {
  opacity: 1;
  clip-path: inset(0 0% 0 0);
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.slide.transitioning {
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.slide-bg-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
  transform: scale(1.1);
  transition: transform 0.8s ease-out;
}

.slide.active .slide-bg-image {
  transform: scale(1);
}

.slide-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.3));
  z-index: 2;
}

.slide-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  z-index: 3;
}

.slide-text {
  max-width: 800px;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.slide.active .slide-title {
  font-size: 4rem;
  font-weight: 900;
  line-height: 1.2;
  margin-bottom: 30px;
  background: linear-gradient(45deg, #ffffff, var(--primary-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: slideInFromLeft 0.8s ease-out 0.3s both;
}

.slide.active .slide-subtitle {
  font-size: 1.4rem;
  line-height: 1.6;
  margin-bottom: 40px;
  color: rgba(255, 255, 255, 0.9);
  animation: slideInFromLeft 0.8s ease-out 0.5s both;
}

.slide.active .slide-buttons {
  display: flex;
  gap: 20px;
  animation: slideInFromLeft 0.8s ease-out 0.7s both;
}

.slide-title {
  font-size: 4rem;
  font-weight: 900;
  line-height: 1.2;
  margin-bottom: 30px;
  background: linear-gradient(45deg, #ffffff, var(--primary-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  opacity: 0;
  transform: translateX(-50px);
}

.slide-subtitle {
  font-size: 1.4rem;
  line-height: 1.6;
  margin-bottom: 40px;
  color: rgba(255, 255, 255, 0.9);
  opacity: 0;
  transform: translateX(-50px);
}

.slide-buttons {
  display: flex;
  gap: 20px;
  opacity: 0;
  transform: translateX(-50px);
}

.slider-controls {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
  padding: 0 30px;
  z-index: 4;
}

.slider-btn {
  width: 60px;
  height: 60px;
  border: none;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

.slider-dots {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 15px;
  z-index: 4;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dot.active {
  background: var(--primary-color);
  border-color: var(--primary-color);
  transform: scale(1.2);
}

/* 新しいスライダーナビゲーション */
.slider-navigation {
  position: absolute;
  bottom: 100px;
  right: 50px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  z-index: 4;
}

.nav-btn {
  display: flex;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 50px;
  padding: 0;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  overflow: hidden;
  min-width: 80px;
  height: 50px;
}

.nav-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateX(-10px);
}

.nav-btn.active {
  background: rgba(139, 199, 128, 0.3);
  border: 2px solid var(--primary-color);
}

.nav-btn-left {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  font-weight: bold;
  font-size: 0.9rem;
  border-radius: 50%;
  margin-right: 15px;
  flex-shrink: 0;
}

.nav-btn.active .nav-btn-left {
  background: var(--primary-color);
  color: white;
}

.nav-btn-right {
  position: relative;
  color: white;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  padding-right: 20px;
  min-width: 100px;
  text-align: left;
  overflow: hidden;
}

.progress-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  background: var(--primary-color);
  transition: width 0.1s linear;
  border-radius: 1px;
}

.nav-btn.active .progress-bar {
  background: var(--primary-color);
  box-shadow: 0 0 10px var(--primary-color);
}

/* ワイプアニメーション */
@keyframes wipeIn {
  0% {
    clip-path: inset(0 100% 0 0);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    clip-path: inset(0 0% 0 0);
    opacity: 1;
  }
}

@keyframes wipeOut {
  0% {
    clip-path: inset(0 0% 0 0);
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    clip-path: inset(0 0% 0 100%);
    opacity: 0;
  }
}

/* コンテンツアニメーション */
@keyframes slideInFromLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(50px);
  }
}

/* レスポンシブデザイン */
@media (max-width: 768px) {
  .slide-title {
    font-size: 2.5rem;
  }

  .slide-subtitle {
    font-size: 1.2rem;
  }

  .slide-buttons {
    flex-direction: column;
    gap: 15px;
  }

  .slider-controls {
    padding: 0 20px;
  }

  .slider-btn {
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }

  .slider-navigation {
    bottom: 120px;
    right: 20px;
  }

  .nav-btn {
    min-width: 60px;
    height: 40px;
  }

  .nav-btn-left {
    width: 40px;
    height: 40px;
    font-size: 0.8rem;
    margin-right: 10px;
  }

  .nav-btn-right {
    font-size: 0.8rem;
    min-width: 80px;
    padding-right: 15px;
  }
}

@media (max-width: 480px) {
  .slide-title {
    font-size: 2rem;
  }

  .slide-subtitle {
    font-size: 1.1rem;
  }

  .slide-text {
    padding: 0 20px;
  }

  .slider-controls {
    padding: 0 15px;
  }

  .slider-btn {
    width: 45px;
    height: 45px;
    font-size: 1rem;
  }

  .slider-navigation {
    bottom: 100px;
    right: 15px;
  }

  .nav-btn {
    min-width: 50px;
    height: 35px;
  }

  .nav-btn-left {
    width: 35px;
    height: 35px;
    font-size: 0.7rem;
    margin-right: 8px;
  }

  .nav-btn-right {
    font-size: 0.7rem;
    min-width: 70px;
    padding-right: 12px;
  }
} 