.about {
  background: linear-gradient(135deg, #f8f9fa 0%, #fff 100%);
}

.about-content {
  display: flex;
  flex-direction: column;
  gap: 60px;
}

.about-intro {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  align-items: center;
  margin-bottom: 40px;
}

.intro-image {
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.intro-image img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.intro-image:hover img {
  transform: scale(1.05);
}

.intro-text h3 {
  font-size: 2rem;
  color: var(--text-primary);
  margin-bottom: 20px;
  font-weight: 700;
}

.intro-text p {
  color: var(--text-secondary);
  line-height: 1.8;
  font-size: 1.1rem;
}

.about-vision {
  display: flex;
  justify-content: center;
}

.vision-card {
  max-width: 600px;
  width: 100%;
  padding: 40px;
  text-align: center;
}

.card-header {
  margin-bottom: 30px;
}

.card-header h3 {
  font-size: 2rem;
  color: var(--primary-color);
  font-weight: 700;
  margin-bottom: 20px;
}

.vision-text {
  font-size: 2.5rem;
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 20px;
  background: linear-gradient(45deg, var(--primary-color), var(--primary-dark));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.vision-description {
  font-size: 1.2rem;
  color: var(--text-secondary);
  line-height: 1.6;
}

.about-purposes {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 30px;
}

.purpose-item {
  padding: 40px;
  text-align: center;
  transition: all 0.3s ease;
}

.purpose-item:hover {
  transform: translateY(-5px);
}

.purpose-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  display: block;
}

.purpose-item h4 {
  font-size: 1.4rem;
  color: var(--text-primary);
  margin-bottom: 20px;
  font-weight: 700;
}

.purpose-item p {
  color: var(--text-secondary);
  line-height: 1.6;
}

.about-values {
  text-align: center;
}

.values-title {
  font-size: 2rem;
  color: var(--text-primary);
  margin-bottom: 40px;
  font-weight: 700;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 30px;
}

.value-item {
  padding: 40px;
  text-align: center;
  transition: all 0.3s ease;
}

.value-item:hover {
  transform: translateY(-5px);
}

.value-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  display: block;
}

.value-item h4 {
  font-size: 1.4rem;
  color: var(--text-primary);
  margin-bottom: 20px;
  font-weight: 700;
}

.value-item p {
  color: var(--text-secondary);
  line-height: 1.6;
}

/* レスポンシブデザイン */
@media (max-width: 768px) {
  .about-content {
    gap: 40px;
  }

  .about-intro {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .intro-text {
    text-align: center;
  }

  .intro-text h3 {
    font-size: 1.8rem;
  }

  .vision-card {
    padding: 30px;
  }

  .vision-text {
    font-size: 2rem;
  }

  .about-purposes {
    grid-template-columns: 1fr;
  }

  .values-grid {
    grid-template-columns: 1fr;
  }

  .purpose-item,
  .value-item {
    padding: 30px;
  }

  .purpose-icon,
  .value-icon {
    font-size: 2.5rem;
  }
}

@media (max-width: 480px) {
  .vision-text {
    font-size: 1.8rem;
  }

  .vision-description {
    font-size: 1.1rem;
  }

  .purpose-item h4,
  .value-item h4 {
    font-size: 1.2rem;
  }

  .purpose-item,
  .value-item {
    padding: 20px;
  }
} 