.services {
  background: linear-gradient(135deg, #fff 0%, #f8f9fa 100%);
}

.services-overview {
  margin-bottom: 60px;
}

.overview-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  align-items: center;
}

.overview-text h3 {
  font-size: 2rem;
  color: var(--text-primary);
  margin-bottom: 20px;
  font-weight: 700;
}

.overview-text p {
  color: var(--text-secondary);
  line-height: 1.8;
  font-size: 1.1rem;
}

.overview-images {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  position: relative;
}

.overview-img-1,
.overview-img-2 {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 15px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.overview-img-1 {
  transform: translateY(-10px);
}

.overview-img-2 {
  transform: translateY(10px);
}

.overview-img-1:hover,
.overview-img-2:hover {
  transform: translateY(0px) scale(1.05);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-top: 60px;
}

.service-card {
  padding: 40px;
  text-align: center;
  transition: all 0.3s ease;
  position: relative;
}

.service-card:hover {
  transform: translateY(-10px);
}

.service-icon {
  font-size: 4rem;
  margin-bottom: 30px;
  display: block;
  animation: bounce 2s infinite;
}

.service-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 20px;
}

.service-description {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 30px;
  font-size: 1.1rem;
}

.service-features {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.service-features li {
  position: relative;
  padding-left: 30px;
  color: var(--text-secondary);
  font-size: 0.95rem;
  line-height: 1.5;
}

.service-features li::before {
  content: '✓';
  position: absolute;
  left: 0;
  top: 0;
  color: var(--primary-color);
  font-weight: bold;
  font-size: 1.1rem;
}

/* 個別のサービスカードのアクセント */
.service-card:nth-child(1) .service-icon {
  color: #FF6B6B;
}

.service-card:nth-child(2) .service-icon {
  color: #4ECDC4;
}

.service-card:nth-child(3) .service-icon {
  color: #45B7D1;
}

.service-card:nth-child(4) .service-icon {
  color: #96CEB4;
}

.service-card:nth-child(5) .service-icon {
  color: #FFEAA7;
}

.service-card:nth-child(1)::before {
  background: linear-gradient(90deg, #FF6B6B, #FF8E8E);
}

.service-card:nth-child(2)::before {
  background: linear-gradient(90deg, #4ECDC4, #6ED3CC);
}

.service-card:nth-child(3)::before {
  background: linear-gradient(90deg, #45B7D1, #67C5D7);
}

.service-card:nth-child(4)::before {
  background: linear-gradient(90deg, #96CEB4, #A8D5C1);
}

.service-card:nth-child(5)::before {
  background: linear-gradient(90deg, #FFEAA7, #FFECB3);
}

/* アニメーション */
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* レスポンシブデザイン */
@media (max-width: 768px) {
  .overview-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .overview-text {
    text-align: center;
  }

  .overview-text h3 {
    font-size: 1.8rem;
  }

  .overview-images {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .overview-img-1,
  .overview-img-2 {
    transform: translateY(0);
  }

  .services-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .service-card {
    padding: 30px;
  }

  .service-icon {
    font-size: 3rem;
  }

  .service-title {
    font-size: 1.3rem;
  }

  .service-description {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .service-card {
    padding: 20px;
  }

  .service-icon {
    font-size: 2.5rem;
  }

  .service-title {
    font-size: 1.2rem;
  }

  .service-features li {
    font-size: 0.9rem;
  }
} 